.contact-section {
  background-color: var(--white);
  padding: 80px 0;
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.contact-info {
  flex: 1;
  min-width: 300px;
}

.contact-form-container {
  flex: 2;
  min-width: 300px;
}

/* Contact info styles */
.info-item {
  display: flex;
  margin-bottom: 30px;
}

.info-icon {
  width: 50px;
  height: 50px;
  background-color: rgba(74, 144, 226, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  color: var(--primary-color);
  font-size: 1.2rem;
}

.info-content h3 {
  margin: 0 0 5px 0;
  font-size: 1.2rem;
  color: var(--text-color);
}

.info-content p {
  margin: 0;
  color: #666;
  line-height: 1.5;
}

/* Contact form styles */
.contact-form-container {
  background-color: var(--light-bg);
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.form-message {
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: center;
}

.form-message.success {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.form-message.error {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

.form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.form-group label {
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--text-color);
}

.required {
  color: #e74c3c;
}

.form-group input,
.form-group textarea {
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
}

.submit-button {
  display: inline-block;
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 12px 28px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px;
}

.submit-button:hover {
  background-color: #3a7bc8;
}

/* Responsive styles */
@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
    gap: 15px;
    margin-bottom: 15px;
  }
} 